.page-body {
  min-width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}
.page-header {
  position: relative;
  width: 100%;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 1200px) {
  .page-header {
    background-color: rgba(0, 0, 0, 0.3);
  }
}
.page-header.page-header--opened {
  background-color: #283645;
}
@media (min-width: 1200px) {
  .page-header.page-header--opened {
    background-color: rgba(0, 0, 0, 0.3);
  }
}
.page-header--closed {
  background-color: rgba(0, 0, 0, 0.3);
}
.page-header--opened .page-header__top {
  background-color: #283645;
}
@media (min-width: 1200px) {
  .page-header--opened .page-header__top {
    background-color: transparent;
  }
}
.page-header__wrapper {
  position: relative;
  top: 0;
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 1200px) {
  .page-header__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1200px;
    padding: 0 130px;
  }
}
.page-header__top {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  height: 69px;
}
@media (min-width: 660px) {
  .page-header__top {
    align-items: flex-start;
    height: 110px;
  }
}
@media (min-width: 1200px) {
  .page-header__top {
    padding-left: 0;
    border: none;
    background-color: transparent;
  }
}
.page-header__logo {
  padding-top: 5px;
  color: #ffffff;
}
.page-header__logo-tablet {
  display: none;
}
.page-header__logo-desktop {
  display: none;
}
.page-header__logo:hover {
  color: #d22856;
}
.page-header__logo:active {
  color: #ffffff;
  opacity: 0.3;
}
@media (min-width: 660px) {
  .page-header__logo {
    padding-top: 27px;
  }
  .page-header__logo-tablet {
    display: block;
  }
  .page-header__logo-mobile {
    display: none;
  }
}
@media (min-width: 1200px) {
  .page-header__logo-tablet {
    display: none;
  }
  .page-header__logo-mobile {
    display: none;
  }
  .page-header__logo-desktop {
    display: block;
  }
}
.main-nav {
  display: block;
}
.main-nav--closed {
  display: none;
}
@media (min-width: 1200px) {
  .main-nav--closed {
    display: block;
  }
}
.main-nav--opened {
  display: block;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
  border-top: 1px solid #000000;
}
@media (min-width: 1200px) {
  .main-nav__list {
    display: flex;
    justify-content: space-between;
    border: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    margin-left: 20px;
    padding-right: 59px;
  }
  .main-nav__item:last-child {
    padding-right: 0;
  }
}
.main-nav__link {
  position: relative;
  display: block;
  padding: 13px 0 17px 0;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  text-transform: uppercase;
  transition: 0.3s;
  color: #ffffff;
  border-bottom: 1px solid #000000;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
}
.main-nav__link:hover,
.main-nav__link:focus {
  color: #d22856;
}
.main-nav__link:active {
  color: #ffffff;
  opacity: 0.3;
}
.main-nav__link--active:hover,
.main-nav__link--active:focus,
.main-nav__link--active:active {
  color: #ffffff;
}
@media (min-width: 1200px) {
  .main-nav__link {
    margin: 0;
    line-height: 18px;
    font-weight: 400;
    text-transform: none;
    border: none;
  }
  .main-nav__link--active::after {
    content: "";
    position: absolute;
    margin: auto;
    top: 30px;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 2px;
    z-index: 20;
    background-color: #ffffff;
  }
}
.main-nav__toggle {
  display: block;
  position: absolute;
  top: 22px;
  right: 20px;
  width: 50px;
  height: 24px;
  padding: 0;
  border: none;
  z-index: 9;
  background: 0 0;
  cursor: pointer;
  outline: none;
}
.main-nav__toggle--hide {
  display: none;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 41px;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__toggle:hover > span,
.main-nav__toggle:hover > span::after,
.main-nav__toggle:hover > span::before {
  background: #d22856;
  transition: 0.3s;
}
.main-nav__toggle-line {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  height: 4px;
  font-size: 0;
  color: transparent;
  background: #ffffff;
  transition: 0.3s;
}
.main-nav__toggle-line::after {
  top: 10px;
}
.main-nav__toggle-line::before {
  top: -10px;
}
.main-nav__toggle-line::after,
.main-nav__toggle-line::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background: #ffffff;
  transform-origin: 50% 50%;
  transition: 0.3s, transform 0.3s;
}
.main-nav__toggle--closed .main-nav__toggle-line {
  background: transparent;
}
.main-nav__toggle--closed .main-nav__toggle-line::after,
.main-nav__toggle--closed .main-nav__toggle-line::before {
  transform: rotate3d(0, 0, 1, 45deg);
  transition: 0.3s, transform 0.3s 0.3s;
  top: 0;
  left: 24px;
  width: 29px;
  height: 3px;
}
.main-nav__toggle--closed .main-nav__toggle-line::before {
  transform: rotate3d(0, 0, 1, -45deg);
}
.main-nav__toggle--closed .main-nav__toggle-line.main-nav__toggle-line:hover {
  background: transparent;
}
