.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  bottom: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset 0%;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.input {
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  min-height: 63px;
  padding-left: 20px;
  border: 2px solid #e5e5e5;
  border-radius: 2px;
  font-size: 18px;
  line-height: 30px;
}
.input:hover {
  outline: 2px solid #283645;
}
.input:active,
.input:focus {
  outline: 2px solid #d22856;
}
