  color: #283645;
  opacity: 0.7;
}
.radio {
  position: relative;
}
.radio:hover {
  cursor: pointer;
}
.radio__icon {
  position: absolute;
  top: -4px;
  left: 0;
  width: 35px;
  height: 35px;
  background-color: #ffffff;
  border: 2px solid #e5e5e5;
  border-radius: 50%;
}
.radio__input:checked + .radio__icon::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  width: 15px;
  height: 15px;
  background-color: #d22856;
  border-radius: 50%;
}
.radio__input:hover + .radio__icon {
  border: 2px solid #283645;
}
.radio__input:focus + .radio__icon {
  cursor: pointer;
  border: 2px solid #283645;
}
.radio__input:active + .radio__icon {
  border: 2px solid #d22856;
}
.radio__text {
  font-size: 18px;
  line-height: 30px;
}
