.page-header {
  position: relative;
  width: 100%;
  z-index: 3;
  background-color: @page-header-top-bg;

  @media (min-width: @desktop-width) {
    background-color: @page-header-top-bg;
  }
}

.page-header.page-header--opened {
  background-color: @main-text-color;

  @media (min-width: @desktop-width) {
    background-color: @page-header-top-bg;
  }
}

.page-header--closed {
  background-color: @page-header-top-bg;
}

.page-header--opened .page-header__top {
  background-color: @main-text-color;

  @media (min-width: @desktop-width) {
    background-color: transparent;
  }
}

.page-header__wrapper {
  position: relative;
  top: 0;
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto;
  // background-color: @main-text-color;

  @media (min-width: @desktop-width) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1200px;
    padding: 0 130px;
  }
}

.page-header__top {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  height: 69px;
  // background-color: @main-text-color;
  // background-color: transparent;
  // border-bottom: 1px solid @title-text-color;

  @media (min-width: @tablet-width) {
    align-items: flex-start;
    height: 110px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 0;
    border: none;
    background-color: transparent;
  }
}

.page-header__logo {
  padding-top: 5px;
  color: @nav-text-color;

  &-tablet {
    display: none;
  }

  &-desktop {
    display: none;
  }

  &:hover {
    color: @accent-color;
  }

  &:active {
    color: @nav-text-color;
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    padding-top: 27px;

    &-tablet {
      display: block;
    }

    &-mobile {
      display: none;
    }
  }

  @media (min-width: @desktop-width) {
    &-tablet {
      display: none;
    }

    &-mobile {
      display: none;
    }

    &-desktop {
      display: block;
    }
  }
}
